import React from 'react';
import styled from "styled-components";

import {Menu} from "../../Common/Menu/Menu";
import {Header} from "../../Common/Header/Header";
import {OrderTitle} from "./Title/OrderTitle";
import {useModalMenu} from "../../Hooks/useModalMenu";
import {ModalMenu} from "../../Common/Menu/ModalMenu";

const Container = styled.div`
    height: 100vh;
    display: flex;
`;

const OrderMenu = styled.section`
    background: #151B1F;
    width: 4%;
    height: 100%;
`;

const OrderContent = styled.section`
    width: 96%;
    height: 100%;
`;

const HeaderContainer = styled.div`
    padding: 32px;
`;

const NavContainer = styled.div``;


export const OrderPage = () => {

    const modalMenu = useModalMenu();

    return (
        <React.Fragment>
            <Container>
                { modalMenu.active ? <ModalMenu mainPage={false} { ...modalMenu}/> : ''}
                <OrderMenu>
                    <Menu {...modalMenu}/>
                </OrderMenu>
                <OrderContent>
                    <HeaderContainer className='container'>
                        <Header/>
                    </HeaderContainer>
                    <NavContainer className='container-fluid'/>
                    <OrderTitle className='container-xl'/>
                </OrderContent>
            </Container>
        </React.Fragment>
    );
}
